.footer-title {
    // font-family: luckiest-guy;
    letter-spacing: 1.3px;
    margin-inline-end: 5px;
}

.edea-link {
    margin-inline-start: 5px;

    &:hover {
        color: $clr3;
    }
}

.footer-container {
    width: 100%;
    background-color: $clr1;
    border-top: 3px solid $clr3;
}

.footer-subcontainer {
    width: 100%;
    height: 47px;
    justify-content: center;
    align-items: center;
}

.credit-container {
    height: 30px;
}

.social-icons-container {
    height: 30px;
    width: 10%;
    margin-inline-start: 20px;
}

.social-icon {
    &.MuiSvgIcon-root {
        height: 24px;
        width: 24px;
    }
    &:hover {
        fill: $clr3;
    }
}

.github-icon {
    &.MuiSvgIcon-root {
        height: 20px;
        width: 20px;
    }
    &:hover {
        fill: $clr3;
    }
}

.social-icon-container:not(:last-child) {
    margin-inline-end: 10px;
}

@include for-normal-layout {
    .footer-container {
        height: 100px;
        padding-top: 10px;
    }

    .footer-subcontainer {
        flex-direction: column;
        height: 80px;
    }

    .credit-container {
        height: 80px;
        flex-direction: column;
    }
}

@include for-tablet-layout {
    .footer-subcontainer {
        font-size: rem(15px);
        height: 70px;
    }

    .credit-container {
        margin-top: 10px;
    }

    .social-icons-container {
        margin-inline-start: unset;
    }
}

@include for-mobile-wide-layout {
    .footer-subcontainer {
        font-size: rem(14px);
        margin-top: 5px;
    }
    .social-icons-container {
        width: unset;
    }
    .credit-container {
        margin-top: unset;
        margin-bottom: 10px;
        flex-direction: column;
    }
}

@include for-mobile-narrow-layout {
    .social-icons-container {
        width: unset;
    }
    .credit-container {
        margin-bottom: unset;
    }
}
